$white-color: #fff;
$black-color: #000;
$pink-color: #ff70d9;
$yellow-color: #ebff00;

.app {
  overflow: hidden;
  box-sizing: border-box;
  min-height: 100vh;
  color: $white-color;
  background: linear-gradient(
    180deg,
    rgba(13, 30, 72, 1) 0%,
    rgba(48, 69, 113, 1) 100%
  );
}

@mixin section() {
  max-width: 1248px;
  margin: auto;
  padding: 0 80px;
}

@mixin section_tablet() {
  max-width: 768px;
  padding-right: 30px;
  padding-left: 30px;
}

@mixin section_mobile() {
  max-width: 360px;
  padding-right: 20px;
  padding-left: 20px;
}

@mixin backgroundImg($imgPath: url(./assets/wideMilkyWay.png)) {
  background: linear-gradient(
      270deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(15, 20, 32, 0.79) 100%
    ),
    $imgPath no-repeat center/cover;
}

.burger-menu {
  position: relative;
  display: none;
}

.burger-menu__icon {
  position: relative;
  width: 30px;
  height: 22px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  cursor: pointer;
  z-index: 1000;

  &-item {
    display: block;
    width: 100%;
    height: 2px;
    background: $white-color;
    transition: all 0.3s ease;
  }

  &.open &-item:nth-child(1) {
    transform: rotate(45deg) translate(6px, 5px);
  }

  &.open &-item:nth-child(2) {
    opacity: 0;
  }

  &.open &-item:nth-child(3) {
    transform: rotate(-45deg) translate(6px, -5px);
  }
}

.burger-menu__nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 270px;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 999;

  &.open {
    transform: translateX(0);
  }
}

.header {
  background-color: $black-color;
}

.header__nav {
  @include section;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;

  .header__logo img {
    height: 35px;
  }

  .header__nav-list {
    display: flex;
    gap: 55px;

    .nav__item {
      list-style: none;
      .nav__link {
        text-decoration: none;
        color: $white-color;
        font-weight: bold;
        font-size: 18px;
        transition: 0.2s ease;

        .cart-icon {
          height: 20px;
          width: 24px;
          background: $white-color;

          mask: url(./assets/white_cart.svg);
          -webkit-mask: url(./assets/white_cart.svg);
          &:hover {
            background: linear-gradient(
              62deg,
              $yellow-color 0%,
              $pink-color 100%
            );
            opacity: 60%;
          }
        }

        &:hover {
          background-image: linear-gradient(
            62deg,
            $yellow-color 0%,
            $pink-color 100%
          );
          background-clip: text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
    }
  }
}

.banner {
  @include backgroundImg();
}

.banner-wrapper {
  @include section;
  height: 490px;
  display: flex;
  justify-content: space-between;
  padding-top: 60px;
  padding-bottom: 60px;
  .text-block {
    margin-top: 30px;
  }

  .banner__image {
    height: 400px;
    margin-right: -24px;
  }
}

.text-block {
  max-width: 635px;

  &__title {
    margin: 0;
    font-size: 72px;
    font-weight: 900;
    line-height: 100%;
  }
  &__paragraph {
    margin: 22px 0;
    font-size: 32px;
    font-weight: 900;
  }
  &__button {
    background-color: $yellow-color;
    color: black;
    border-radius: 100px;
    padding: 14px 40px;
    font-size: 22px;
    line-height: 24px;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
    letter-spacing: -1px;
    transition: 0.2s ease;
    border: 1px solid $yellow-color;
    &:hover {
      opacity: 60%;
    }
  }
}

.offers {
  @include section();
  padding-top: 58px;
  padding-bottom: 64px;
  & > h2 {
    font-size: 32px;
    font-weight: 900;
    margin: 0 0 40px;
  }
}

.offers-wrapper {
  display: flex;
  gap: 40px;
  flex-wrap: wrap;

  .offer {
    padding: 50px 35px 36px;
    flex: 1;
    border-radius: 10px;
    .text-block {
      &__title {
        font-size: 46px;
        width: 280px;
      }
      &__paragraph {
        font-size: 24px;
      }
      &__button {
        border: 1px solid $yellow-color;
        background-color: rgba(0, 0, 0, 0);
        color: $yellow-color;
      }
    }
  }

  .pink_nebula,
  .blue_yello_nebula {
    flex: 1 0 calc(100% - 70px);
    padding-top: 35px;
    .text-block {
      &__title {
        font-size: 74px;
        width: 100%;
      }
      &__paragraph {
        font-size: 26px;
      }
    }
  }
}

.offer__text-small {
  display: none;
}

.pink_nebula {
  @include backgroundImg($imgPath: url(./assets/pinkNebula.svg));
}
.orion_nebula {
  @include backgroundImg($imgPath: url(./assets/orionNebula.svg));
}
.colorful_galaxy {
  @include backgroundImg($imgPath: url(./assets/colorfulGalaxy.png));
}
.blue_yello_nebula {
  @include backgroundImg($imgPath: url(./assets/blueYellowNebula.svg));
}

.journey {
  @include section();
  padding-bottom: 60px;

  .jorney-title {
    font-size: 32px;
    font-weight: 900;
    margin: 0;
  }
  .jorney-paragraph {
    font-size: 20px;
    font-weight: bold;
    line-height: 24px;
    opacity: 60%;
    margin: 17px 0;
  }
  .jorney-link {
    text-decoration: none;
    color: $yellow-color;
    font-weight: 900;
    font-size: 20px;
    transition: 0.2s ease;

    &:hover {
      opacity: 60%;
    }
  }
}

.footer {
  height: 127px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: $black-color;
  padding-bottom: 19px;
  color: $yellow-color;

  &__text {
    font-size: 18px;
    line-height: 28px;
    font-weight: 900;
    margin: 0;
  }
}

.highlight--yellow {
  color: $yellow-color;
}

.highlight--pink {
  color: $pink-color;
}

@media (max-width: 1060px) {
  .banner-wrapper {
    .banner__image {
      height: 368px;
      margin-right: -184px;
    }
    .text-block {
      &__title {
        font-size: 56px;
      }
      &__paragraph {
        font-size: 22px;
      }
    }
  }
}

@media (max-width: 768px) {
  .header__nav {
    @include section_tablet();

    .header__logo img {
      height: 25px;
    }
  }

  .banner-wrapper {
    padding: 0;
    @include section_tablet();
    height: 420px;

    .text-block {
      margin-top: 100px;
    }

    .banner__image {
      margin-right: -134px;
      margin-top: 26px;
    }
    .text-block {
      &__title {
        font-size: 48px;
      }
      &__paragraph {
        font-size: 22px;
      }
    }
  }

  .offers {
    @include section_tablet();
    padding-top: 55px;
    padding-bottom: 55px;
    & > h2 {
      font-size: 24px;
      margin: 0 0 30px;
    }

    .offers-wrapper {
      gap: 30px;

      .offer,
      .pink_nebula,
      .blue_yello_nebula {
        padding: 105px 25px 26px;
        flex: 1;
        border-radius: 10px;
        .text-block {
          &__title {
            font-size: 40px;
            width: 245px;
          }
          &__paragraph {
            font-size: 16px;
          }
          &__button {
            padding: 7px 40px;
            font-size: 16px;
            line-height: 19px;
          }
        }
      }
      .pink_nebula {
        .text-block {
          &__title {
            width: 185px;
          }
        }
      }
      .offer__text-large {
        display: none;
      }
      .offer__text-small {
        display: block;
      }
    }
  }

  .journey {
    @include section_tablet();
    padding-bottom: 53px;

    .jorney-title {
      font-size: 24px;
    }
    .jorney-paragraph {
      font-size: 16px;
      line-height: 19px;
      margin: 13px 0 20px;
    }
    .jorney-link {
      font-size: 16px;
    }
  }
}

@media (max-width: 680px) {
  .offers {
    @include section_mobile();
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

@media (max-width: 570px) {
  .burger-menu {
    display: block;

    .header__nav-list {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 30px;
      padding: 0;

      .nav__item {
        .nav__link {
          font-size: 22px;
        }
      }
    }
  }

  .header__nav-wrapper {
    display: none;
  }

  .banner {
    background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(15, 20, 32, 0.79) 100%
      ),
      url(./assets/wideMilkyWay.png) no-repeat center/cover;
  }

  .banner-wrapper {
    overflow: hidden;
    @include section_mobile();
    height: 450px;
    flex-direction: column-reverse;
    .banner__image {
      margin: -3px -5px;
    }
    .text-block {
      margin: 0 0 40px;
      text-align: center;
      &__title {
        font-size: 36px;
      }
      &__paragraph {
        font-size: 16px;
        margin: 17px 0;
      }
      &__button {
        font-size: 16px;
        line-height: 19px;
        padding: 7px 40px;
      }
    }
  }
}

@media (max-width: 360px) {
  .header__nav {
    @include section_mobile();
    height: 48px;

    .header__logo img {
      height: 25px;
    }
  }

  .journey {
    @include section_mobile();
    padding-bottom: 50px;

    .jorney-title {
      font-size: 18px;
    }
    .jorney-paragraph {
      font-size: 14px;
      line-height: 17px;
      margin: 20px 0;
    }
    .jorney-link {
      font-size: 14px;
    }
  }
}
